import React, { useState } from 'react';
import { View, TextInput, Button, Text, StyleSheet, Modal, Alert } from 'react-native';
import { Picker } from '@react-native-picker/picker';
import { RNCamera } from 'react-native-camera';

export default function AuthForm() {
  const [firstname, setFirstname] = useState('');
  const [secondname, setSecondname] = useState('');
  const [year, setYear] = useState('');
  const [groupe, setGroupe] = useState('');
  const [university, setUniversity] = useState('University of Algiers Ben Youssef Ben Khadda');
  const [faculty, setFaculty] = useState('Faculte de Medecine');
  const [department, setDepartment] = useState('Medecine');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [message, setMessage] = useState('');
  const [isSignUp, setIsSignUp] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [qrData, setQrData] = useState(null);
  const [showCamera, setShowCamera] = useState(false);

  const handleSignUp = async () => {
    if (password !== confirmPassword) {
      setMessage('Passwords do not match.');
      return;
    }

    try {
      const response = await fetch('http://localhost/CARAMA/signup.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: `firstname=${encodeURIComponent(firstname)}&secondname=${encodeURIComponent(secondname)}&year=${encodeURIComponent(year)}&groupe=${encodeURIComponent(groupe)}&university=${encodeURIComponent(university)}&faculty=${encodeURIComponent(faculty)}&department=${encodeURIComponent(department)}&email=${encodeURIComponent(email)}&password=${encodeURIComponent(password)}`,
      });

      const data = await response.json();
      setMessage(data.message || 'An unexpected error occurred.');
    } catch (error) {
      setMessage('An error occurred. Please try again later.');
    }
  };

  const handleLogin = async () => {
    try {
      const response = await fetch('http://localhost/CARAMA/login.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: `email=${encodeURIComponent(email)}&password=${encodeURIComponent(password)}`,
      });

      if (!response.ok) {
        setMessage('Login request failed. Please try again.');
        return;
      }

      const data = await response.json();
      setMessage(data.message || 'An unexpected error occurred.');
      if (data.message === 'Login successful!') {
        setIsLoggedIn(true);
        setShowCamera(true); // Open QR scanner after login
      }
    } catch (error) {
      setMessage('An error occurred. Please try again later.');
    }
  };

  const handleBarCodeRead = ({ data }) => {
    setShowCamera(false);
    setQrData(data);
    Alert.alert(
      "QR Code Found",
      `Data: ${data}`,
      [
        { text: "Confirm", onPress: () => sendQrDataToServer(data) },
        { text: "Cancel", style: "cancel", onPress: () => setShowCamera(true) }
      ]
    );
  };

  const sendQrDataToServer = async (data) => {
    try {
      const response = await fetch('http://localhost/CARAMA/qr_process.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: `qrData=${encodeURIComponent(data)}`,
      });
      const result = await response.json();
      setMessage(result.message || 'Data sent successfully.');
    } catch (error) {
      setMessage('Failed to send QR data. Please try again.');
    }
  };

  return (
    <View style={styles.container}>
      {!isLoggedIn ? (
        <>
          {isSignUp && (
            <>
              <TextInput placeholder="First name" value={firstname} onChangeText={setFirstname} style={styles.input} />
              <TextInput placeholder="Second name" value={secondname} onChangeText={setSecondname} style={styles.input} />
              <TextInput placeholder="Year" value={year} onChangeText={setYear} style={styles.input} />
              <TextInput placeholder="Group" value={groupe} onChangeText={setGroupe} style={styles.input} />
            </>
          )}
          <TextInput placeholder="Email" value={email} onChangeText={setEmail} style={styles.input} keyboardType="email-address" autoCapitalize="none" />
          <TextInput placeholder="Password" secureTextEntry value={password} onChangeText={setPassword} style={styles.input} />
          {isSignUp && <TextInput placeholder="Confirm Password" secureTextEntry value={confirmPassword} onChangeText={setConfirmPassword} style={styles.input} />}

          {isSignUp && (
            <>
              <Picker selectedValue={university} onValueChange={setUniversity} style={styles.picker}>
                <Picker.Item label="University of Algiers Ben Youssef Ben Khadda" value="University of Algiers Ben Youssef Ben Khadda" />
              </Picker>
              <Picker selectedValue={faculty} onValueChange={setFaculty} style={styles.picker}>
                <Picker.Item label="Faculte de Medecine" value="Faculte de Medecine" />
              </Picker>
              <Picker selectedValue={department} onValueChange={setDepartment} style={styles.picker}>
                <Picker.Item label="Medecine" value="Medecine" />
                <Picker.Item label="Medecine Dentaire" value="Medecine Dentaire" />
                <Picker.Item label="Pharmacie" value="Pharmacie" />
              </Picker>
            </>
          )}
          <Button title={isSignUp ? 'Sign Up' : 'Login'} onPress={isSignUp ? handleSignUp : handleLogin} />
          <Text style={styles.toggleText}>
            {isSignUp ? 'Already have an account? ' : 'Don\'t have an account? '}
            <Text style={styles.toggleLink} onPress={() => setIsSignUp(!isSignUp)}>
              {isSignUp ? 'Login' : 'Sign Up'}
            </Text>
          </Text>
        </>
      ) : null}

      {showCamera && (
        <RNCamera style={styles.camera} onBarCodeRead={handleBarCodeRead} captureAudio={false} />
      )}

      {message && <Text style={styles.message}>{message}</Text>}
    </View>
  );
}

const styles = StyleSheet.create({
  container: { padding: 20 },
  input: { height: 40, borderColor: '#ccc', borderWidth: 1, marginBottom: 10, paddingHorizontal: 10 },
  picker: { height: 50, borderColor: '#ccc', borderWidth: 1, marginBottom: 10 },
  toggleText: { marginTop: 10, textAlign: 'center' },
  toggleLink: { color: 'blue', fontWeight: 'bold' },
  message: { marginTop: 10, color: 'red', textAlign: 'center' },
  camera: { flex: 1, justifyContent: 'flex-end', alignItems: 'center' }
});
